  <template>
  <div class="card card-body mt-4">
    <h1 class="my-3">Editar Producto</h1>
    <form @submit.prevent="update">
      <div class="form-group">
        <label>Nombre</label>
        <input v-model="form.nombre" class="form-control" required />
      </div>

      <div class="form-group mt-3">
        <label>Cantidad</label>
        <input
          v-model="form.cantidad"
          class="form-control"
          type="number"
          required
        />
      </div>

            <div class="form-group mt-3">
        <label>Precio</label>
        <input
          v-model="form.precio"
          class="form-control"
          type="cantidad"
          required
        />
      </div>

      <button type="submit" class="btn btn-primary  mt-3">
        Actualizar
      </button>
    </form>
  </div>
</template>

<script>
import { reactive, computed, onMounted } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { getItem, updateItem } from '@/firebase'

export default {
  setup() {
    const router = useRouter()
    const route = useRoute()
    const itemId = computed(() => route.params.id)
    const form = reactive({ nombre: '', cantidad: undefined, precio: null })

    onMounted(async () => {
      const item = await getItem(itemId.value)
      form.nombre = item.nombre
      form.cantidad = item.cantidad
      form.precio = item.precio
    })

    const update = async () => {
      form.cantidad = Number(form.cantidad)
      await updateItem(itemId.value, { ...form })
      router.push('/stock/')
      form.nombre = ''
      form.cantidad = null
      form.precio = null
    }

    return { form, update }
  }
}
</script>
